.main-intro {
  background-image: url("../img/intro/intro-background.jpg");
  background-repeat: no-repeat;
  background-position: top center;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/intro/intro-background@2x.jpg");
    background-size: 640px auto;
  }

  @media (min-width: $tablet-width) {
    background: $background-white;
  }

  @media (min-width: $desktop-width) {
    &::before {
      position: absolute;
      z-index: 2;
      content: "";
      width: 100%;
      height: 526px;
      background: linear-gradient(to right, $background-white 50%, rgba(104, 183, 56, 0.85) 50%);
    }

    &::after {
      position: absolute;
      top: 0;
      left: 50%;
      z-index: 1;
      content: "";
      width: 720px;
      height: 694px;
      background-image: url("../img/intro/intro-background-desktop.jpg");
      background-repeat: no-repeat;

      @media (min-resolution: 2dppx) {
        background-image: url("../img/intro/intro-background-desktop@2x.jpg");
        background-size: 1440px auto;
      }
    }
  }

  &::before {
    @media (max-width: $mobile-width-only) {
      position: absolute;
      content: "";
      width: 100%;
      height: 302px;
      background-color: $background-image;
    }
  }
}

.main-intro__wrapper {
  position: relative;
  width: 280px;
  margin: 0 auto;
  padding: 27px 20px 0;
  text-align: center;

  @media (min-width: $tablet-width) {
    width: 621px;
    padding: 32px 30px 22px 117px;
    text-align: left;
  }

  @media (min-width: $desktop-width) {
    z-index: 4;
    width: 1060px;
    padding: 40px 190px;
  }
}

.main-intro__title {
  margin: 0;
  margin-bottom: 25px;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  color: $color-text-white;

  @media (min-width: $tablet-width) {
    width: 416px;
    margin-bottom: 41px;
    font-size: 60px;
    line-height: 60px;
    color: $color-text-black;
  }

  @media (min-width: $desktop-width) {
    margin-top: 14px;
    color: $color-text-black;
  }
}

.main-intro__slogan {
  margin: 0;
  margin-bottom: 29px;
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;
  color: $color-text-white;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 20px;
    color: $color-text-black;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 13px;
    color: $color-text-black;
  }
}

.main-intro__image {
  display: block;

  @media (min-width: $tablet-width) {
    position: absolute;
    top: 235px;
    left: 28px;
  }

  @media (min-width: $desktop-width) {
    position: absolute;
    top: -28px;
    left: 560px;
    z-index: 4;
  }
}
